@keyframes fullWidth {
    100% {
        width: 90%;
    }
}

@keyframes fullWidth1200 {
    100% {
        width: 90%;
    }
}

@keyframes fullWidth992 {
    100% {
        width: 95%;
    }
}

@keyframes fullWidth768 {
    100% {
        width: 100%;
    }
}

@keyframes popup {
    1% {
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}